import { Fragment, useState } from "react";
import { Container, Row, Col } from "reactstrap";
import Img1 from "../../img/fta4.jpg";
import ModalVideo from "react-modal-video";
import "../../../node_modules/react-modal-video/scss/modal-video.scss";
export default function MeetArtist() {
  const [isOpen, setOpen] = useState(false);
  return (
    <Fragment>
      <section className="section bg-light bg-features">
        <Container>
          <Row className="align-items-center">
            <Col lg={5}>
              <div className="mt-4 home-img">
                <div className="animation-2"></div>
                <div className="animation-3"></div>
                <img
                  src={Img1}
                  className="img-fluid rounded"
                  alt="ALEJANDRO GLATT"
                />
              </div>
            </Col>
            <Col lg={6} className="offset-lg-1">
              <div className="mt-4">
                <h2 style={{ fontWeight: "bold" }}>ALEJANDRO GLATT</h2>
                {/* <a href="https://youtu.be/h8yVJz4EUXo">FEEL THE FRUIT</a> */}
                <p className="mt-4 text-muted">
                  IS A TULUM BASED MULTIDISCIPLINARY ARTIST,
                  <br /> PASSIONATE ABOUT THE AESTHETIC AND ENERGETIC
                  INTERACTIONS BETWEEN FRUITS,
                  <br />
                  COLORS AND HUMANS FOUNDER OF FEEL THE FRUIT MOVEMENT, <br />
                  AN EXPERIENTIAL HEALING EXPERIENCE THAT COMBINES MEDITATION
                  <br /> WITH FRUIT TO CONNECT TO THE FIVE SENSES AND <br />
                  HELP OTHERS FEEL GROUNDED AND INSPIRED.
                  <br />{" "}
                  <a href="https://alejandroglatt.com/">
                    WWW.ALEJANDROGLATT.COM
                  </a>
                  <br />{" "}
                  <a href="https://instagram.com/ALEJANDROGLATT">
                    @ALEJANDROGLATT
                  </a>
                </p>
                <div className="mt-4">
                  <ModalVideo
                    channel="youtube"
                    autoplay
                    isOpen={isOpen}
                    videoId="h8yVJz4EUXo"
                    onClose={() => setOpen(false)}
                  />

                  <button className="btn btn-primary mr-3" onClick={() => setOpen(true)}>
                  FEEL THE FRUIT
                  </button>
                  {/* <iframe
                    className="shadow"
                    width="100%"
                    height="auto"
                    src="https://www.youtube.com/embed/h8yVJz4EUXo"
                    title="YouTube video player"
                    frameBorder={0}
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen
                  /> */}
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
    </Fragment>
  );
}
