import React, { Fragment } from "react";
import { Container, Row, Col } from "reactstrap";
import useCountdown from "../../hooks/useCountdown";
import moment from "moment-timezone";
import HomeUrl from "../../assets/images/home-border.png";
export default function CountDown() {
  const EST = moment.tz("2021-10-26 13:00", "America/New_York").format();
  const countdown = useCountdown(new Date(EST));
  return (
    <Fragment>
      <section className="section bg-services" id="services">
        <Container>
          <Row>
            <Col lg="12">
              <div className="title-box text-center">
                <h3 className="title-heading mt-4">
                  CryptoPapayas  Launching In{" "}
                </h3>
                <img src={HomeUrl} height="15" className="mt-3" alt="" />
              </div>
            </Col>
          </Row>
          <Row className="mt-5 pt-4 text-center">
            <Col lg={3}>
              <div className="p-4 mt-4">
                <div className="media">
                  <div className="count-box1 bg-soft-primary text-center">
                    <h3 className="counter-count mb-1 text-primary">{countdown.days}</h3>
                    <p className="text-uppercase text-muted mb-0 f-14">
                     Day
                    </p>
                  </div>
                </div>
              </div>
            </Col>
            <Col lg={3}>
              <div className="p-4 mt-4">
                <div className="media">
                  <div className="count-box1 bg-soft-primary text-center">
                    <h3 className="counter-count mb-1 text-primary">{countdown.hours}</h3>
                    <p className="text-uppercase text-muted mb-0 f-14">
                      Hours
                    </p>
                  </div>
                </div>
              </div>
            </Col>
            <Col lg={3}>
              <div className="p-4 mt-4">
                <div className="media">
                  <div className="count-box1 bg-soft-primary text-center">
                    <h3 className="counter-count mb-1 text-primary">{countdown.minutes}</h3>
                    <p className="text-uppercase text-muted mb-0 f-14">
                       Minutes
                    </p>
                  </div>
                </div>
              </div>{" "}
            </Col>
            <Col lg={3}>
              <div className="p-4 mt-4">
                <div className="media">
                  <div className="count-box1 bg-soft-primary text-center">
                    <h3 className="counter-count mb-1 text-primary">{countdown.seconds}</h3>
                    <p className="text-uppercase text-muted mb-0 f-14">
                    Seconds
                    </p>
                  </div>
                </div>
              </div>{" "}
            </Col>
          </Row>
          
        </Container>
      </section>
    </Fragment>
  );
}
