import React, { Component } from "react";
import NFT from "../img/nft.png"
import { Container, Row, Col, Form, Button, Input } from "reactstrap";
import Solana from "../img/solana.svg"
import LogoDark from "../../assets/images/logo-dark.png";

function Footer() {
  return(
    <React.Fragment>
    {/* Footer Start */}
    <footer className="section bg-light bg-footer pb-5">
      <Container>
      <Row>
      <Col lg={6}>
       <div className="text-center">
       <h3>As seen on <a href="https://nftcalendar.io/event/crypto-papayas/"><img style={{
         width: "20%"
       }} src={NFT} alt="nft cal" /></a></h3>
       
       </div>
      </Col>
      <Col lg={6}>
       <div className="mt-5 text-center">
       <h3>Powered By <img 
       className="img-fluid"
       style={{
         width: "9%",
         verticalAlign: "sub"
       }}
       src={Solana} alt="solana" /></h3>
       
       </div>
      </Col>
      </Row>
        <hr className="my-5" />
        <Row>
        <Col lg={12}>
          <div className="text-center">
            <p className="text-muted mb-0">{(new Date().getFullYear())}{" "} Â© Crypto Papayas</p>
          </div>
        </Col>
      </Row>
      </Container>
    </footer>
    {/* Footer End */}
  </React.Fragment>
  )
}
export default Footer;
