import { Fragment } from "react";
import { Row, Col, Container } from "react-bootstrap";
import HomeUrl from "../../assets/images/home-border.png";
import img1 from "../../img/1.jpg";
import img2 from "../../img/2.jpg";
import img3 from "../../img/3.jpg";
import img4 from "../../img/4.jpg";
import img5 from "../../img/5.jpg";
import img6 from "../../img/6.jpg";
export default function Meet() {
  return (
    <Fragment>
      <section className="section bg-services" id="info">
        <Container>
          <Row>
            <Col lg="12">
              <div className="title-box text-center">
                <h3 className="title-heading mt-4">MEET THE CRYPTOPAPAYAS</h3>
                <img src={HomeUrl} height="15" className="mt-3" alt="" />
              </div>
            </Col>
          </Row>
          <Row className="mt-5 pt-4 text-center">
            <Col lg={4}>
              <div className="services-box p-4 mt-4">
                <img className="img-fluid rounded" src={img1} alt="crypto papayas" />
              </div>
            </Col>
            <Col lg={4}>
              <div className="services-box p-4 mt-4">
                <img className="img-fluid rounded" src={img2} alt="crypto papayas" />
              </div>
            </Col>
            <Col lg={4}>
              <div className="services-box p-4 mt-4">
                <img className="img-fluid rounded" src={img3} alt="crypto papayas" />
              </div>
            </Col>
            <Col lg={4}>
              <div className="services-box p-4 mt-4">
                <img className="img-fluid rounded" src={img4} alt="crypto papayas" />
              </div>
            </Col>
            <Col lg={4}>
              <div className="services-box p-4 mt-4">
                <img className="img-fluid rounded" src={img5} alt="crypto papayas" />
              </div>
            </Col>
            <Col lg={4}>
              <div className="services-box p-4 mt-4">
                <img className="img-fluid rounded" src={img6} alt="crypto papayas" />
              </div>
            </Col>
          </Row>
        </Container>
      </section>
    </Fragment>
  );
}
