import React, { Component } from "react";
import { Link } from "react-router-dom";

import { Col, Container, Row } from "reactstrap";
import Slider from "react-slick";

import HomeUrl from "../../assets/images/home-border.png";

import Team1 from "../../img/team1.jpeg";
import Team2 from "../../img/team2.jpeg";
import Team3 from "../../img/team3.jpeg";
import Team4 from "../../img/team4.jpeg";

export default class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      teams: [
        {
          id: 1,
          img: Team1,
          name: "Chris Needham",
          nickname: "CryptoPapayas",
          instagram: "https://instagram.com/afreechris",
          linkedin: "http://www.linkedin.com/in/christopher-needham-72593a7",
        },
        {
          id: 2,
          img: Team2,
          name: "Saras Paudel",
          nickname: "CryptoPapayas",
          linkedin: "https://www.linkedin.com/in/saraspaudel/"
        },
        {
          id: 3,
          img: Team3,
          name: "Marcelin Kamdoum",
          nickname: "CryptoPapayas",
          twitter: "https://twitter.com/pitdogeking",
          linkedin: "https://www.linkedin.com/in/marcelin-kamdoum-97b249210/"
        },
        {
          id: 4,
          img: Team4,
          name: "Alejandro Glatt",
          nickname: "CryptoPapayas",
          instagram: "https://instagram.com/alejandroglatt",
        },
      ],
    };
  }

  render() {
    var temsettings = {
      autoplay: true,
      dots: true,
      speed: 300,
      infinite: false,
      arrows: false,
      slidesToShow: 2,
      slidesToScroll: 1,
    };

    const teamslides = this.state.teams.map((team, teamindex) => {
      return (
        <div className="team-box p-3" key={teamindex}>
          <Row className="align-items-center">
            <Col lg={6}>
              <div className="mt-4">
                <h5 className="mt-2">{team.name}</h5>
                <div className="team-social mt-4 pt-2">
                  <ul className="list-inline mb-0">
                    {team.instagram ? (
                      <>
                        <li className="list-inline-item">
                          <a href={team.instagram} className="text-reset">
                            <i className="mdi mdi-instagram"></i>
                          </a>
                        </li>
                      </>
                    ) : (
                      ""
                    )}
                    {team.twitter ? (
                      <>
                        <li className="list-inline-item">
                          <a href={team.twitter} className="text-reset">
                            <i className="mdi mdi-twitter"></i>
                          </a>
                        </li>
                      </>
                    ) : (
                      ""
                    )}
                   {team.linkedin ? (
                      <>
                        <li className="list-inline-item">
                          <a href={team.linkedin} className="text-reset">
                            <i className="mdi mdi-linkedin"></i>
                          </a>
                        </li>
                      </>
                    ) : (
                      ""
                    )}
                  </ul>
                </div>
              </div>
            </Col>
            <Col lg={6}>
              <div className="mt-4">
                <img src={team.img} alt="" className="img-fluid rounded" />
              </div>
            </Col>
          </Row>
        </div>
      );
    });
    return (
      <React.Fragment>
        <section className="section pt-0" id="team">
          <Container>
            <Row>
              <Col lg="12">
                <div className="title-box text-center">
                  <h3 className="title-heading mt-4">THE TEAM </h3>
                  <img src={HomeUrl} height="15" className="mt-3" alt="" />
                </div>
              </Col>
            </Row>
            <Row className="mt-5 pt-4">
              <Col lg="12">
                <div className="team-carousel">
                  <Slider {...temsettings}>{teamslides}</Slider>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
      </React.Fragment>
    );
  }
}
