import axios from "axios";
import { Fragment, useEffect, useState } from "react";

interface Resp {
    total: number
}
export default function RSVPStats() {
    const [data, setData] = useState<Resp>();

    const getData = async() => {
        try {
            const response = await axios.get('https://crypto-backend-production.up.railway.app/api/rsvp')
            setData(response.data)
        } catch (error) {
            console.log(error);
        }
    }
    useEffect(() => {
        getData();
    },[getData])
    return(
        <Fragment>
        <div className="p-5 m-auto text-center">
            <h3>Total whitelist Enroll {data?.total}</h3>
        </div>
        </Fragment>
    )
}