import React, { Fragment, useState } from "react";
import {
  WalletDialogButton,
  WalletDisconnectButton,
} from "@solana/wallet-adapter-material-ui";
import { useAnchorWallet } from "@solana/wallet-adapter-react";
import axios from "axios";
import Button from "@mui/material/Button";
import useMediaQuery from "@mui/material/useMediaQuery";
import { Container } from "react-bootstrap";
import swal from "sweetalert";
import Loading from "../hooks/useLoading";
import WOW from "../hooks/confetti";

function Connect(props) {
  const matches = useMediaQuery("(max-width:720px)");
  const [item, setItem] = useState("");
  // const [email, setEmail] = useState("");
  const [check, setCheck] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [cong, setCong] = useState(false);

  const disc = () => {
    const el = document.createElement("div");
    el.innerHTML =
      "DUE TO THE BETA NATURE OF SOLANA, PHONES ARE NOT YET SUPPORTED. PLEASE USE GOOGLE CHROME OR FIREFOX WITH A SOLANA WALLET EXTENSION. FOR MORE INFORMATION,&nbsp;<a href='https://medium.com/@schimmer_42836/how-to-mint-crypto-papayas-f85f9f2bede1'>please check out this article.</a>";

    swal({
      icon: "info",
      title: "Disclaimer",
      content: el,
    });
    // swal(
    //   "Disclaimer Message!",
    //   "Due to the Beta Nature of Solana, Mobile Compatibility is Limited. Please Use Google Chrome or Firefox with a Solana Wallet Extension. For More Information.",
    //   "info",
    // );
  };
  const wallet = useAnchorWallet();
  // console.log();
  const checkChange = () => {
    setCheck(!check);
  };
  const rsvpButton = async () => {
    let address = wallet.publicKey.toBase58();
    const body = {
      pubkey: address,
    };
    setLoading(true);
    try {
      const response = await axios.post(
        "https://crypto-backend-production.up.railway.app/api/rsvp",
        body
      );
      setItem(response.data);
      setCong(true);
     console.log(response)
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };
  if (item) {
    setTimeout(() => {
      setCong(false);
    }, 9000);
  }
console.log(wallet)
  return (
    <Fragment>
    {cong && <WOW />}
      {!wallet ? (
        <>
          <Container>
            <WalletDialogButton
              className="btn btn-primary mr-3">
              {" "}
              Connect Wallet to whitelist
            </WalletDialogButton>
          </Container>
        </>
      ) : (
        <>
          <p>
            <input
              style={{
                marginRight: "10px",
              }}
              defaultChecked={check}
              onChange={checkChange}
              type="checkbox"
            />
            I will mint on October 26 5 PM (UTC) to receive <br /> passive
            Solana royalties on future mints.
          </p>
          {check && (
            <>
              {!item ? (
                <>
                  {isLoading === false ? (
                    <>
                      <Button
                      onClick={rsvpButton}
                        className="btn btn-primary mr-3"
                        style={{
                          color: "#fff"
                        }}
                      >
                        whitelist now
                      </Button>
                    </>
                  ) : (
                    ""
                  )}
                  {isLoading && <Loading />}
                </>
              ) : (
                <h3>{item.mssg}</h3>
              )}
            </>
          )}
        </>
      )}
    </Fragment>
  );
}
export default Connect;
