import { Fragment } from "react";
import { Container, Row, Col } from "reactstrap";
import { Link } from "react-router-dom";

import HomeUrl from '../../assets/images/home-border.png';
import Img from '../../img/rsvp.png';
import Connect from "../../components/Connect";

export default function RSVPWELCOME() {
    return(
        <Fragment>
        <section className="bg-home bg-light" id="home">
          <div className="home-center">
            <div className="home-desc-center">
              <Container>
                <Row className="align-items-center">
                  <Col lg={6}>
                    <div className="home-content">
                      <h3 className="mb-0">#CRYPTOPAPAYAS</h3>
                      <img src={HomeUrl} height="15" alt="" />
                      <h1 style={{ fontWeight: "bold"}} className="home-title mt-4">Whitelist now for royalties</h1>
                      <p className="text-muted mt-4 f-20">Whitelist your wallet now and mint on Oct. 26 5 PM (UTC) to receive automatic passive solana royalties on future mints and secondary sales.</p>
                      <div className="mt-4 pt-2">
                        <Connect />
                      </div>
                    </div>
                  </Col>
                  <Col lg={6}>
                    <div className="home-img mt-5">
                      <div className="animation-1"></div>
                      <div className="animation-2"></div>
                      <div className="animation-3"></div>
                      <img src={Img} className="img-fluid" alt="" />
                    </div>
                  </Col>
                </Row>
              </Container>
            </div>
          </div>
        </section>
        </Fragment>
    )
}