import React, { Component } from "react";
import { Col, Container, Row } from "reactstrap";

import Slider from "react-slick";

import img1 from "../../img/fta-1.jpeg";
import img2 from "../../img/fta-2.jpeg";
import img3 from "../../img/fta-3.jpg";
import img4 from "../../img/fa4.jpg";
import img5 from "../../img/fa5.jpeg";
import img6 from "../../img/fa6.jpg";
import img7 from "../../img/fa7.jpeg";
import img8 from "../../img/fa8.jpeg";

import HomeUrl from "../../assets/images/home-border.png";

export default class Artist extends Component {
  constructor(props) {
    super(props);
    this.state = {
      slideitems: [
        {
          id: 100,
          img: img1,
          name: "Camimi",
          designation: "FEATURED ARTIST 1",
          instagram: "https://www.instagram.com/camimi_isdope",
        },
      ],
    };
  }
  render() {
    var slidesettings = {
      dots: true,
      speed: 300,
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
      autoplay: true,
      // dotsClass: "slick-dots slick-thumb carousel-indicators",
      // customPaging: function (i) {
      //   return (
      //     <img src={process.env.PUBLIC_URL + '/images/img-' + (i+1) +'.jpg'} alt="" className=" testi-img img-fluid rounded mx-auto d-block" />
      //   );
      // },
    };

    const clientslides = this.state.slideitems.map((slideitem, clientkey) => {
      return (
        <div className="carousel-item" key={slideitem.id}>
          <Row className="align-items-center">
            <Col lg="3">
              <div className="client-box mt-4">
                {/* <h5 className="line-height_1_6">{slideitem.description}</h5> */}
                {/* <div className="client-icon">
                  <i className="mdi mdi-format-quote-open"></i>
                </div> */}
                <h5 className="f-18">{slideitem.name} </h5>
                <p className="text-primary mb-0">- {slideitem.designation} </p>
                {/* <div className="client-icon">
                  <i className="mdi mdi-format-quote-close"></i>
                </div> */}
                <div className="team-social mt-4 pt-2">
                  <ul className="list-inline mb-0">
                    {slideitem.instagram ? (
                      <>
                        <li className="list-inline-item">
                          <a href={slideitem.instagram} className="text-reset">
                            <i className="mdi mdi-instagram"></i>
                          </a>
                        </li>
                      </>
                    ) : (
                      ""
                    )}
                  </ul>
                </div>
              </div>
            </Col>
            <Col lg={9}>
              <div className="text-center mt-4">
              <Row>
                <Col lg={4}>
                <img src={slideitem.img} className="img-fluid rounded mt-5" alt={slideitem.name} />
                </Col>
                <Col lg={4}>
                <img src={img2} className="img-fluid rounded mt-5" alt={slideitem.name} />
                </Col>
                <Col lg={4}>
                <img src={img3} className="img-fluid rounded mt-5" alt={slideitem.name} />
                </Col>
                </Row>
              </div>
            </Col>
          </Row>
        </div>
      );
    });

    return (
      <React.Fragment>
        <section className="section" id="clients">
          <Container>
            <Row>
              <Col lg={12}>
                <div className="title-box text-center">
                  <h3 className="title-heading mt-4">FEATURED ARTIST</h3>
                  {/* <p className="text-muted f-17 mt-3">Vivamus ac nulla ultrices laoreet neque mollis mi morbi
                  elementum mauris
                            sit amet arcu <br /> fringilla auctor In eleifend maximus nisi sed vulputate.</p> */}

                  <img src={HomeUrl} height="15" className="mt-3" alt="" />
                </div>
              </Col>
            </Row>
            <Row className="mt-5 pt-4">
              <Col lg="12">
                <div
                  id="carouselExampleIndicators"
                  className="carousel slide"
                  data-ride="carousel"
                >
                  <div className="carousel-inner">
                    <Slider {...slidesettings}>{clientslides}</Slider>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
      </React.Fragment>
    );
  }
}
