import { Fragment, useEffect, useState } from "react";
import {
  Navbar,
  Nav,
  NavbarBrand,
  NavbarToggler,
  NavItem,
  NavLink,
  Container,
  Collapse,
} from "reactstrap";
import { FaInstagram } from "react-icons/fa";
import { FiTwitter } from "react-icons/fi";
import { RiDiscordLine } from "react-icons/ri";

import logoimg from "../../logo.svg";
import {
  WalletDialogButton,
  WalletDisconnectButton,
} from "@solana/wallet-adapter-material-ui";
import { useAnchorWallet } from "@solana/wallet-adapter-react";

export default function MenuBar() {
  const wallet = useAnchorWallet();
  const [header, setHeader] = useState();
  const [isopen , setIsOpen] = useState(false)

  const listenScrollEvent = (event) => {
    if (window.scrollY < 73) {
      return setHeader(true);
    } else if (window.scrollY > 101) {
      return setHeader(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", listenScrollEvent);

    return () => window.removeEventListener("scroll", listenScrollEvent);
  }, []);
const toggle = () => {
  setIsOpen(!isopen);
};

  return (
    <Fragment>
      <Navbar
        expand="lg"
        fixed=""
        className="navbar-custom sticky sticky-dark"
        id="navbar"
      >
        <Container>
          <NavbarBrand className="navbar-brand logo text-uppercase" href="/">
            <img src={logoimg} alt="" height="60" />
          </NavbarBrand>
          <ul 
          style={{display: "flex", flexDirection: "row"}} className="navbar-nav navbar-center">
            <li className="nav-item">
              <a
                href="https://www.instagram.com/cryptopapayas/"
                className="nav-link"
              >
                <FaInstagram size="2rem" />
              </a>
            </li>
            <li className="nav-item">
              <a href="https://twitter.com/cryptopapayas" className="nav-link">
                <FiTwitter size="2rem" />
              </a>
            </li>
            <li className="nav-item">
              <a
                href="https://discord.com/invite/cryptopapayas"
                className="nav-link"
              >
                <RiDiscordLine size="2rem" />
              </a>
            </li>
          </ul>
          <div className="navbar-button d-none d-lg-inline-block">
            {!wallet ? (
              <WalletDialogButton className="btn btn-sm btn-soft-primary btn-round"> Connect Wallet</WalletDialogButton>
            ) : (
              <WalletDisconnectButton className="btn btn-sm btn-soft-primary btn-round" />
            )}
          </div>
        </Container>
      </Navbar>
    </Fragment>
  );
}
